.gallery {
  padding-bottom: 123px;
  max-width: 1280px;
  padding-left: 115px;
  padding-right: 115px;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  background: linear-gradient(to bottom, $bg-section 40%, $main-color 40%);

  @media screen and (max-width: 767px) {
    padding-bottom: 80px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-bottom: 121px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

.gallery h2 {
  font-size: 0;
  opacity: 0;
}

.masonry__thumb img {
  border-radius: 15px;

  @media screen and (min-width: 768px) {
    width: 100%;
    height: auto;
  }
}

.grid__list {
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 4px;

  @media screen and (max-width: 767px) {
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(6, 1fr);
  }

  @media screen and (min-width: 768px) {
    grid-gap: 8px;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(9, 1fr);
    grid-gap: 7px;
  }
}

.grid__item {
  transition: transform 1500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.grid__item:hover {
  @media screen and (max-width: 767px) {
    transform: rotate(1turn) scale(1.35);
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    transform: rotate(1turn) scale(1.55);
  }

  @media screen and (min-width: 1280px) {
    transform: rotate(1turn) scale(1.55);
  }
}

.grid__item-1 {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;

  @media screen and (max-width: 767px) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 4;
    grid-row-end: 7;
  }

  @media screen and (min-width: 768px) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 4;
    grid-row-end: 7;
  }
}

