@font-face {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('../fonts/titan-one-v8-latin-regular.eot');
  src: local(''), url('../fonts/titan-one-v8-latin-regular.eot?#iefix') format('embedded-opentype'),
    url('../fonts/titan-one-v8-latin-regular.woff2') format('woff2'),
    url('../fonts/titan-one-v8-latin-regular.woff') format('woff'),
    url('../fonts/titan-one-v8-latin-regular.ttf') format('truetype'),
    url('../fonts/titan-one-v8-latin-regular.svg#TitanOne') format('svg');
}

@font-face {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
