  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url('../fonts/dm-sans-v6-latin-500.eot');
  src: local(''), url('../fonts/dm-sans-v6-latin-500.eot?#iefix') format('embedded-opentype'),
    url('../fonts/dm-sans-v6-latin-500.woff2') format('woff2'),
