body {
  font-family: 'DM Sans', sans-serif;
}

h1 {
  font-family: 'Titan One', cursive;
  font-size: 58px;
  line-height: 1.13;
  text-align: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}
