.contacts {
  padding-bottom: 63px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  background: linear-gradient(rgb(255, 255, 255) 5%, rgb(255, 245, 246) 0px);

  @media screen and (min-width: 768px) {
    // display: inline-block;
    background: linear-gradient(rgb(255, 255, 255) 12%, rgb(255, 245, 246) 0px);
    // padding-top: 119px;
    padding-bottom: 108px;
  }

  @media screen and (min-width: 1280px) {
    // display: inline-block;
    background: linear-gradient(rgb(255, 255, 255) 12%, rgb(255, 245, 246) 0px);
    // padding-top: 132px;
    padding-bottom: 115px;
  }
}

.contacts__card-list {
  display: inline-block;
  text-align: left;

  @media screen and (min-width: 768px) {
    display: inline-flex;
  }
  @media screen and (min-width: 1280px) {
    display: inline-flex;
  }
}

.contacts__card-item {
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    margin-right: 20px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 30px;
    margin-bottom: 0;
  }
}

.contacts__card-item:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

.contacts__article {
  width: 220px;
  height: 427px;
  background: $main-color;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  padding: 43px 20px;

  @media screen and (min-width: 768px) {
    height: 544px;
    padding: 50px 32px;
  }

  @media screen and (min-width: 1280px) {
    width: 330px;
    height: 572px;
    padding: 51px 43px 62px 44px;
  }
}
