.contacts__subtitle {
  width: 80px;
  height: 16px;
  margin: 0;

  font-family: $main-font;
  font-weight: $font-medium;
  font-style: normal;
  font-size: 12px;
  line-height: 1.86;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  letter-spacing: 0.04em;

  background-color: $ice-coffee;
  color: $main-color;
  border-radius: 4px;

  @media screen and (min-width: 768px) {
    width: 73px;
    height: 28px;
    font-size: 16px;
  }

  @media screen and (min-width: 1280px) {
    width: 73px;
    height: 28px;
    font-size: 16px;
  }
}

.contacts__subtitle--green {
  width: 130px;
  background-color: $milkshakes;
  @media screen and (min-width: 768px) {
    width: 138px;
  }
  @media screen and (min-width: 1280px) {
    width: 138px;
  }
}

.contacts__address-title {
  margin-top: 25px;
  margin-bottom: 0;
  font-family: $main-font;
  font-style: normal;
  font-weight: $font-medium;

  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  color: $head-text;
  @media screen and (min-width: 768px) {
    margin-top: 30px;
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    display: block;
    margin-top: 32px;
    font-size: 16px;
  }
}

.contacts__address-subtitle {
  width: 179px;
  margin-bottom: 25px;
  margin-top: 10px;
  font-family: $main-font;
  font-style: normal;
  font-weight: $font-medium;
  font-size: 12px;
  line-height: 1.5;
  color: $main-text;

  @media screen and (min-width: 768px) {
    margin-bottom: 30px;
    font-size: 14px;
    line-height: 1.66;
  }
  @media screen and (min-width: 1280px) {
    width: 243px;
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 1.83;
    // letter-spacing: 0.04em;
  }
}

.contacts__list {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
}

.contacts__item {
  font-family: $main-font;
  font-weight: $font-medium;
  font-style: normal;
  font-size: 12px;
  line-height: 1.86;
  color: $head-text;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    letter-spacing: 0.04em;
  }

  &:nth-child(2n) {
    margin-bottom: 10px;
    font-family: $main-font;
    font-weight: $font-medium;
    font-size: 12px;
    line-height: 1.86;
    color: $circle;
    @media screen and (min-width: 768px) {
      margin-bottom: 16px;
      font-size: 14px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 15px;
      font-size: 16px;
    }
  }

  &:first-child {
    margin-top: 25px;

    @media screen and (min-width: 768px) {
      margin-top: 30px;
    }
    @media screen and(min-width: 1280px) {
      margin-top: 32px;
    }
  }

  &:last-child {
    margin-bottom: 25px;
    @media screen and (min-width: 768px) {
      margin-bottom: 30px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 32px;
    }
  }
}

.contacts__address-list {
  margin-top: 25px;
  margin-bottom: 0;
  @media screen and (min-width: 768px) {
    margin-top: 30px;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 32px;
  }
}

.contacts__address-link {
  font-family: $main-font;
  font-weight: $font-medium;
  font-style: normal;
  font-size: 12px;
  line-height: 1.5;
  color: $main-text;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.83;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.83;
  }
}
.contacts__button {
  margin-top: 54px;
  position: relative;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 80px;
  }

  @media screen and (min-width: 1280px) {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 80px;
  }
}

.contacts__button-icon {
  position: absolute;
  top: 17%;
  left: 63%;
  fill: $main-color;
  width: 10px;
  height: 10px;
  border: 2px;
  background-image: url();
  background-repeat: no-repeat;
  @media screen and (min-width: 768px) {
    top: 32%;
    left: 44%;
  }

  @media screen and (min-width: 1280px) {
    top: 32%;
    left: 46%;
  }
}

.contacts__button-icon--accent {
  top: 72%;
  left: 60%;
  width: 10px;
  height: 10px;
  position: absolute;
  border: 2px;
  fill: $accent-color;
  background-image: url('/images/icons/buy.svg');
  background-repeat: no-repeat;
  @media screen and (min-width: 768px) {
    top: 33%;
    left: 69%;
  }

  @media screen and (min-width: 1280px) {
    top: 33%;
    left: 63%;
  }
}

.contacts__button--accent {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  min-width: 162px;
  border: transparent;
  height: 44px;
  background-color: $main-color;
  background: $accent-color;
  border-radius: 22px;
  cursor: pointer;
  @media screen and (min-width: 768px) {
    width: 192px;
    margin-right: 15px;
    margin-left: 0;
  }
  @media screen and (min-width: 1280px) {
    width: 192px;
    margin-right: 15px;
    margin-left: 0;
  }
}

.contacts__button--white {
  display: block;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 10px;
  min-width: 162px;
  border: transparent;
  height: 44px;
  background: $main-color;
  border-radius: 22px;
  transition: $animation;
  cursor: pointer;
  @media screen and (min-width: 768px) {
    width: 192px;
    margin-right: 15px;
    margin-left: 0;
  }
  @media screen and (min-width: 1280px) {
    width: 192px;
    margin-right: 15px;
    margin-left: 0;
  }

  &:hover,
  &:focus {
    background-color: $btn-hover;
  }
}

.contacts__button-text {
  position: relative;
  padding-left: 17px;
  padding-right: 30px;
  font-family: $main-font;
  font-style: normal;
  font-weight: $font-bold;
  font-size: 16px;
  line-height: 1.31;
  color: $main-color;
  @media screen and (min-width: 768px) {
    padding-top: 12px;
    padding-bottom: 12px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 12px;
    // padding-left: 30px;
    padding-bottom: 12px;
  }
}

.contacts__button-text--accent {
  padding-left: 32px;
  padding-right: 50px;
  font-family: $main-font;
  font-style: normal;
  font-weight: $font-bold;
  font-size: 16px;
  line-height: 1.31;
  display: flex;
  align-items: center;
  text-align: center;
  color: $accent-color;
  @media screen and (min-width: 768px) {
    padding-top: 12px;

    padding-bottom: 12px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 12px;
    padding-left: 30px;
    padding-bottom: 12px;
  }
}
